import axios from 'axios'
const api = axios.create()
// api.defaults.baseURL = 'http://enet.api.gg'
api.defaults.baseURL = 'http://SW2018001.trg7.cn'
api.defaults.timeout = 5000
api.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
api.defaults.headers.post['Accept'] = 'application/json'
api.defaults.headers.get['Content-Type'] = 'application/x-www-form-urlencoded'
api.defaults.headers.get['Accept'] = 'application/json'
api.defaults.headers.delete['Content-Type'] = 'application/x-www-form-urlencoded'
api.defaults.headers.delete['Accept'] = 'application/json'
api.defaults.headers.put['Content-Type'] = 'application/x-www-form-urlencoded'
api.defaults.headers.put['Accept'] = 'application/json'
// 请求拦截
api.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么
  // store.commit('SET_LOADING',true);
  // console.log(config)
  let to = sessionStorage.getItem('token')
  // console.log("发送前------ delete   -------" + typeof to)
  if (to !== 'null') {
    config.headers.get.Authorization = "Bearer " + to
    config.headers.post.Authorization = "Bearer " + to
    config.headers.delete.Authorization = "Bearer " + to
    config.headers.put.Authorization = "Bearer " + to
  }
  return config
}, function (error) {
  // 对请求错误做些什么
  // alert('网络错误,请稍后再试')

  // store.commit('SET_LOADING',false);

  return Promise.reject(error)
})

// 添加响应拦截器
api.interceptors.response.use(function (response) {
  // 对响应数据做点什么
  // 加到时器主要是为了 展示Loading效果 项目中应去除
  // console.log("发送后-------------"+sessionStorage.token)
  // store.commit('SET_LOADING',false);
  return response

}, function (err) {
  console.log(err);
  // 对响应错误做点什么
  // store.commit('SET_LOADING',false);
  return Promise.reject(err)
})
export default api

