<template>
  <el-dropdown class="d2-mr">
    <span class="btn-text">你好 {{userInfo.name}}</span>
    <el-dropdown-menu slot="dropdown">
      <el-dropdown-item @click.native="log">
        <d2-icon name="envelope-o" class="d2-mr-5" /> 个人信息
      </el-dropdown-item>
      <el-dropdown-item @click.native="logOff">
        <d2-icon name="power-off" class="d2-mr-5" /> 注销
      </el-dropdown-item>
    </el-dropdown-menu>
    <!-- 个人信息 -->
    <el-dialog title="个人信息" :visible.sync="centerDialogVisible" width="30%" center>
      <el-alert :title="'姓名：'+val.username" type="info" :closable="false" show-icon>
      </el-alert>
      <el-alert :title="'手机号：'+val.phone" type="info" :closable="false" show-icon>
      </el-alert>
      <el-alert :title="'email：'+val.email" type="info" :closable="false" show-icon>
      </el-alert>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="centerDialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>


  </el-dropdown>
</template>

<script>
  import { mapState, mapActions } from 'vuex'
  import { personal } from '../../../../plugin/axios/api.js';
  export default {
    data() {
      return {
        centerDialogVisible: false,
        val: []
      }
    },
    computed: {
      ...mapState({
        userInfo: state => state.d2admin.userInfo
      })
    },
    methods: {
      ...mapActions([
        'd2adminLogout'
      ]),
      /**
       * @description 登出
       */
      logOff() {
        this.d2adminLogout({
          vm: this,
          confirm: true
        })
      },
      log() {
        personal().then(res => {
          console.log(res);
          this.val = res.data;
          this.centerDialogVisible = true;
        })
      }
    }
  }
</script>