
//错误控制

export const Myerror = (res, e) => {
    if (res.status && res.status == 422) {//http422错误
        let str = '';
        for (const key in res.data.errors) {
            console.log(key);
            str += res.data.errors[key][0] + ' '
        }
        e.$message({
            message: res.status + ' ' + str,
            center: true,
            type: 'error'
        });
        return
    } else if (res.status && res.status == 404) {//http404错误
        e.$message({
            message: res.status + ' ' + res.data.message,
            center: true,
            type: 'error'
        });
        return
    } else if (res.status && res.status == 400) {//http400错误
        e.$message({
            message: res.status + ' ' + res.data.message,
            center: true,
            type: 'error'
        });
        return
    } else if (res.status && res.status == 500) {//http400错误
        e.$message({
            message: res.status + ' ' + res.data.message,
            center: true,
            type: 'error'
        });
        return
    }
}

