import axios from './index.js'

//网络层

//获取token

export const mytokens = params => { return axios.post('/api/login', params).then(res => res.data).catch(err => err.response) }

//角色-全部

export const roles = params => { return axios.get('/api/roles', { params }).then(res => res.data).catch(err => err.response) }

//角色-添加

export const add_roles = params => { return axios.post('/api/roles', params).then(res => res.data).catch(err => err.response) }

//角色-删除

export const del_roles = params => { return axios.delete('/api/roles/' + params).then(res => res.data).catch(err => err.response) }

//角色-编辑

export const edit_roles = (params, id) => { return axios.put('/api/roles/' + id, params).then(res => res.data).catch(err => err.response) }

